import React, {useEffect} from 'react'
import {
  CButton,
  CBadge,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CCollapse,
  CLink,
  CModal,
  CModalHeader,
  CModalTitle,
  CModalBody,
  CModalFooter,
  CForm, CFormGroup, CInputGroup, CInput, CInputGroupAppend, CInputGroupText,
} from '@coreui/react'
import {CIcon} from "@coreui/icons-react";
import ReactTags from "react-tag-autocomplete";

const UserDetails = (data) => {
  const [collapsed, setCollapsed] = React.useState(false);
  const [editMode, setEditMode] = React.useState(false);
  const [userData, setUserData] = React.useState( data );

  const [newName, setNewName] = React.useState( data.name );
  const [newEmail, setNewEmail] = React.useState( data.email_address );
  const [newPassword, setNewPassword] = React.useState( "" );

  const [newRoles, setNewRoles] = React.useState( data.roles );

  const [roleSuggestions, setRoleSuggestions] = React.useState( [] );

  useEffect( () => {
    const suggestions = [];

    for ( const app of data.appList ) {
      for ( const role of app.availableRoles ) {
        suggestions.push( { id: role.id, name: app.name + ": " + role.name } );
      }
    }

    setRoleSuggestions( suggestions )
    return () => {}
  }, [data.appList] )

  return (
    <CCol xl={6}>
      <CCard>
        <CCardHeader>
          <CIcon name="cil-user" /> { userData.login } ({userData.email_address})
          <div className="card-header-actions">
            <CLink className="card-header-action" onClick={() => {
              setEditMode(true);
              setNewEmail( userData.email_address );
              setNewName( userData.name );
              setNewRoles( userData.roles );
              setNewPassword( "");
            }}>
              <CIcon name="cil-pencil" />
            </CLink>
            <CLink className="card-header-action" onClick={() => setCollapsed(!collapsed)}>
              <CIcon name={collapsed ? 'cil-chevron-bottom':'cil-chevron-top'} />
            </CLink>
          </div>
        </CCardHeader>
        <CCollapse show={collapsed}>
          <CCardBody>
            <h5>Roles</h5>
            { !!userData.description  && <p  class="lead"><em>{ userData.description}</em></p> }
            { userData.appList.map( (appData) => (
              <div>
              <h6>{appData.name}</h6>
                <h5>{ userData.roles.filter( (r) => r.appId === appData.id ).map( (role) => (<CBadge shape="pill" color="primary">{role.name}</CBadge>) ) }</h5>
              </div>
            ) ) }
          </CCardBody>
        </CCollapse>
      </CCard>
      <CModal
        show={editMode}
        onClose={() => setEditMode(false)}
        size="lg"
      >
        <CForm onSubmit={ () => {
          setEditMode(false);
        }}>
          <CModalHeader closeButton>
            <CModalTitle>Edit details of {userData.name}</CModalTitle>
          </CModalHeader>
          <CModalBody>
            <CFormGroup>
              <CInputGroup>
                <CInput onChange={ (event) => setNewName(event.value) } value={newName} id="username" name="username" placeholder="Username" autoComplete="username"/>
                <CInputGroupAppend>
                  <CInputGroupText><CIcon name="cil-user" /></CInputGroupText>
                </CInputGroupAppend>
              </CInputGroup>
            </CFormGroup>
            <CFormGroup>
              <CInputGroup>
                <CInput onChange={ (event) => setNewEmail(event.value) } value={newEmail} type="email" id="email" name="email" placeholder="Email" autoComplete="email"/>
                <CInputGroupAppend>
                  <CInputGroupText><CIcon name="cil-envelope-closed" /></CInputGroupText>
                </CInputGroupAppend>
              </CInputGroup>
            </CFormGroup>
            <CFormGroup>
              <CInputGroup>
                <CInput onChange={ (event) => setNewPassword(event.value) } value={newPassword} type="password" id="password" name="password" placeholder="Password" />
                <CInputGroupAppend>
                  <CInputGroupText><CIcon name="cil-asterisk" /></CInputGroupText>
                </CInputGroupAppend>
              </CInputGroup>
            </CFormGroup>

            <CFormGroup>
              <CInputGroup>
                <ReactTags onAddition={ ( e ) => {console.log( "add",e )} }  onDelete={ ( e ) => {console.log( "delete",e )} } suggestions={roleSuggestions} />
                <CInputGroupAppend>
                  <CInputGroupText><CIcon name="cil-tag" /></CInputGroupText>
                </CInputGroupAppend>
              </CInputGroup>
            </CFormGroup>


          </CModalBody>
          <CModalFooter>
            <CButton color="secondary" onClick={() => setEditMode(false)}>Cancel</CButton>{' '}
            <CButton type="submit" color="primary">Save</CButton>
          </CModalFooter>
        </CForm>
      </CModal>
    </CCol>
  )
}

export default UserDetails
