import React, { useState, useEffect } from 'react'
import {
  CCard,
  CCardBody,
  CRow, CCol
} from '@coreui/react'
import UserDetails from "./UserDetails";

const Users = () => {
  const [userList, setUserList] = useState({ data: [] })
  const [appList, setAppList] = useState({ data: [] })

  useEffect( () => {
    let ignore = false;
    async function fetchLists() {
      if ( ignore ) {
        return;
      }
      const appLookup = {};

      const appListResponse = await fetch('http://localhost/admin/api/v0/apps', {
        method: 'GET',
      });

      if ( appListResponse.status === 200 ) {
        const appList = await appListResponse.json();
        for ( const app of appList.data ) {
          appLookup[app.id] = app;
        }
        setAppList(appList);
      }

      const userListResponse = await fetch('http://localhost/admin/api/v0/users', {
        method: 'GET',
      });

      if ( userListResponse.status === 200 ) {
        // everything is ok
        const userList = await userListResponse.json();
        for ( const user of userList.data ) {
          const localAppList = [];
          for ( const role of user.roles ) {
            let app = appLookup[role.appId];
            localAppList.push(app);
          }
          localAppList.sort( (a,b) => { return a.name > b.name?1:-1 } );
          user.appList = localAppList;
        }
        setUserList(userList);
      }

    }

    fetchLists();
    return () => { ignore = true }
  }, [] )

  return (
    <>
      <CRow><CCol>
        <CCard>
          <CCardBody>
            stuff
          </CCardBody>
        </CCard>
      </CCol></CRow>
      <CRow> { userList.data.map((userData) => ( <UserDetails {...userData} appList={appList} /> ) ) } </CRow>
    </>
  )
}

export default Users
